*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


.main_div{

  width: 100%;
  height: 100vh;
  background: #23257a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div{
  width: 25%;
  height: 80vh;
  background-color: black;
  color: white;
  box-sizing: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
}

h1{
  color: white;
  background: transparent;
  background-color: #707070;
  padding: 6px 0px 2px 0px;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  color: white;
}

.button{
  min-height: 40px;
  width: 46px;
  border-radius: 50%;
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  font-size: 40px ;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(255, 255, 255, 0.3);
}

.button:hover{
  background-color: rgb(255, 255, 255);
}

ol{
  margin-top: 30px;
}

ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #c5becc;
  text-transform: capitalize;
}

.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-times-circle{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: white;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}


.todo_style .fa-times-circle:hover{
  color: crimson;
}


@media (max-width: 768px){
  .center_div{
    width: 55%;
  }

  .todo_style .fa-times-circle{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  button{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: white;
  }
}

